@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
body {
  font-family: 'Montserrat';
  background-color: #000000;
  margin: 0;
  overflow-x: hidden;
  width: 100vw;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 51px;


}

.header img{
    width: 170px;
    height: auto;
    object-fit: contain;

}

.menu {
    display: flex;
    justify-content: space-evenly;
    width: 40%;
}

.menu .item a {
    color: #FFFFFF;
    font-weight: bold;
    text-decoration: none;
    font-size: 25px;
    letter-spacing: 0;

}

@media screen and (max-width:768px) {
    .header {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .header .menu {
        flex-direction: column;
    }
}
.footer {
    background-color: #ffffff;
    width: 100vw;
    display: flex;
    max-width: -webkit-fill-available;
    justify-content: space-between;
}

.socialMedia {
    display: flex;
    justify-content: space-between;
    padding: 0;
    max-width: 30%;
    margin-top: 40px;
}  

.col-logo p {
    font-size: 12px;
    font-weight: 600;
}

.socialMedia .item {
    list-style-type: none;
}

.footer .first-col {
    display: flex;
    width: 33%;
    align-items: center;
    padding: 32px 51px;
}

.footer .second-col {
    width: 60%;
    height: 100%;
    min-height: 256px;
    display: flex;
    align-items: center;
    background: #f4f4f4;
    padding: 0 50px;
}

.footer .menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0;
}

.footer .menu .item a {
    color: #545454;
    font-size: 12px;
}

.col-menu {
    width: 38%;
    margin-left: 60px;
}

.footer .menu .item {
    list-style-type: none;
    padding: 10px 0;
    border-bottom: 1px solid #8c8b8b;

}

.countries .item {
    list-style-type: none;
    width: auto;
}

.countries {
    display: flex;
    justify-content: space-around;
    width: 100%;
    padding: 0;
}

.countries .item h2 {
    font-size: 20px;
    font-weight: bold;
}

.countries .item p {
    font-size: 12px;
}

@media screen and (max-width: 768px) {
    .footer {
        flex-direction: column;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .footer .first-col {
        flex-direction: column;
        width: 100%;
    }

    .footer .first-col .col-logo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
    }

    .socialMedia {
        max-width: 100%;
        width: 13%;
    }

    .footer .first-col .col-menu {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: 0 auto;
    }

    .footer .first-col .col-menu .item {
        text-align: center;
    }

    .footer .second-col {
        width: 100%;
    }

    .countries {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }
}
.gallery {
    display: flex;
    justify-content: center;
}

.gallery .first-col {
    width: 50%;
    padding: 20px;
}

.gallery .first-col p {
    color: white;
    font-size: 60px;
    height: 50%;
}

.thumbnails {
    display: flex;
    justify-content: space-between;
    padding: 0;

}

.thumbnails .item {
    width: 120px;
    height: 120px;
}

.thumbnails .item img {
    max-width: 100%;
}

.gallery .second-col {
    width: 50%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.second-col img {
    border-radius: 8px;
    max-width: 100%;
}

.first-col .cta {
    padding: 15px 45px;
    border-radius: 20px;
    font-size: 15px;
    font-family: 'Montserrat';
    font-weight: 500;
    position: relative;
}

.first-col .cta span {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 22px;
}

.first-col .cta span:after{
    content: '';
    width: 15px;
    height: 15px;
    display: block;
    position: absolute;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
    right: 0;
}

@media screen and (max-width: 768px) {
    .gallery{
        flex-direction: column;
    }

    .gallery .first-col,
    .gallery .second-col {
        width: 100%;
    }
}
